<template>
  <div>
    <div class="clearfix top-filter">
      <el-button
        class="filter-item fr"
        @click="returnPrev"
      >返回</el-button>
    </div>
    <el-row :gutter="20" style="height: calc(33.3vh - 20px);">
      <el-col v-if="chartIds.includes(1)" :span="8" style="height: 100%;">
        <!-- 库存占比 -->
        <chart-com-1 ref="ChartCom1" :key="viewKey" :title="'库存占比'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(2)" :span="8" style="height: 100%;">
        <!-- 账龄占比 -->
        <chart-com-2 ref="ChartCom2" :key="viewKey" :title="'账龄占比'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(3)" :span="8" style="height: 100%;">
        <!-- 月度库存 -->
        <chart-com-3 ref="ChartCom3" :key="viewKey" :title="'月度库存'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(4)" :span="8" style="height: 100%;">
        <!-- 库存周转率 -->
        <chart-com-4 ref="ChartCom4" :key="viewKey" :title="'库存周转率'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(5)" :span="8" style="height: 100%;">
        <!-- 库存账龄 -->
        <chart-com-5 ref="ChartCom5" :key="viewKey" :title="'库存账龄'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(6)" :span="8" style="height: 100%;">
        <!-- 库存账龄占比 -->
        <chart-com-6 ref="ChartCom6" :key="viewKey" :title="'库存账龄占比'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(7)" :span="8" style="height: 100%;">
        <!-- 在途物资分析 -->
        <chart-com-7 ref="ChartCom7" :key="viewKey" :title="'在途物资分析'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(8)" :span="8" style="height: 100%;">
        <!-- 物料价格分析 -->
        <chart-com-8 ref="ChartCom8" :key="viewKey" :title="'物料价格分析'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(9)" :span="8" style="height: 100%;">
        <!-- 合同到货分析 -->
        <chart-com-9 ref="ChartCom9" :key="viewKey" :title="'合同到货分析'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(10)" :span="8" style="height: 100%;">
        <!-- 供应商综合分析 -->
        <chart-com-10 ref="ChartCom10" :key="viewKey" :title="'供应商综合分析'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col v-if="chartIds.includes(11)" :span="8" style="height: 100%;">
        <!-- 供应商价格趋势 -->
        <chart-com-11 ref="ChartCom11" :key="viewKey" :title="'供应商价格趋势'" v-bind="$attrs" style="height: 100%;" />
      </el-col>
      <el-col :span="8" style="height: 100%;" />
    </el-row>
  </div>
</template>

<script>
// 库存占比 账龄占比 月度库存 库存周转率 库存账龄 库存账龄占比
// const span8 = [1, 2, 3, 4, 5, 6]
// 在途物资分析 物料价格分析 供应商综合分析 供应商价格趋势
// const span12 = [7, 8, 10, 11]
// 合同到货分析
// const span24 = [9]
import ChartComponents from '@/views/screen/boardOverviewV1/components' // 批量引入的chart组件
export default {
  components: {
    ...ChartComponents
  },
  props: {
    ids: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      viewKey: 1
    }
  },
  computed: {
    chartIds() {
      return this.ids.split(',').map(id => Number(id))
    }
  },
  methods: {
    reset() {
      this.viewKey++
      this.$refs.ChartCom1.resize()
      this.$refs.ChartCom2.resize()
      this.$refs.ChartCom3.resize()
      this.$refs.ChartCom4.resize()
      this.$refs.ChartCom5.resize()
      this.$refs.ChartCom6.resize()
    },
    returnPrev() {
      this.$store.dispatch('tagsView/delView', this.$route)
      const { extraInfo } = this.$route.query
      this.$router.push({
        name: extraInfo && extraInfo.id ? 'EditKanban' : 'AddKanban',
        params: extraInfo && extraInfo.id ? { ...extraInfo } : {}
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-col {
    margin-bottom: 20px;
}
</style>

